.listlevers {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin: 20px;
    min-width: 500px;
    max-width: 500px;
   -webkit-justify-content:  space-around;
           justify-content:  space-around;

}
body {
    height: 100vh;
    overflow: hidden;
}
div.timer {
    position: absolute;
    top: 5%;
    right: 4%;
    background-color: rgba(255,255,255,.3);
    width: 130px;
    height: 50px;
    

}
div.timer p {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    font-size: 23px;
    font-weight: 600;
}
p.objective {
    position: absolute;
    font-size: 17px;
    right: 4%;
    top: 13%;
    /* transform: translate(-50%, -50%); */
    font-weight: 600;
    font-weight: bold;
    text-transform: uppercase;
    font-family: Arial;
}

.listlevers.big {
    /* background-color:gray; */
    max-width: 700px;
    min-width: 700px;
}



div.lever {
    margin: 35px;
    position: relative;
    width: 50px;
    height: 100px;
    background-color: white;
    border-radius: 35%;
    transition: 2s;
    border: 2px solid black;
    box-sizing: border-box;
}
button.button {
    display: block;
    border-style: none;
    cursor: pointer;
    outline: none;
    position: absolute;
    left: 50%;
    top: 5px;
    bottom: auto;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 35px;
    height: 35px;
    background-color: #4a4a4a;
    border-radius: 50%;
    transition: all .3s ease;
}
button.button.active {
    top: calc(100% - 35px - 5px);
}
div.alarm {
    position: absolute;
    border-radius: 50%;
  
    width: 35px;
    height: 35px;
    left: 50%;
    -webkit-transform: translate(-50%, -10%);
            transform: translate(-50%, -10%);
    background-image: radial-gradient(gray, red, black)
}
div.leverobj {
    position: relative;
    
}
div.alarm.active {
    background-image: radial-gradient(yellow, white);
}

* {
    margin: 0;
}
h1 {
    text-align: center;
    margin-top: 200px;
    font-size: 60px;
}
h2 {
    position: absolute;
    right: 30%;
}
button.play {
    display: block;
    margin: 100px auto;
    width: 50px;
    height: 50px;
    background-color: black;
    border-style: none;
    border: 2px solid white;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    outline: none;
    box-shadow: 0px 0px 10px 4px rgba(0,0,0,0.71);
    transition: 2s;
    cursor: pointer;
}
button.play:hover {
    -webkit-transform: rotate(1125deg);
            transform: rotate(1125deg);
}

div.menu {
    width: 500px;
    height: 700px;
    background-color: rgba(255,255,255,.6);
   position: absolute;
   top: 50%;
   left: 50%;
   -webkit-transform: translate(-50%,-50%);
           transform: translate(-50%,-50%);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}
div.control {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    width: 200px;
    margin: 20px auto;
}
.menu section {
    
    text-align: center;
}
section p {
    font-size: 30px;
    text-transform: uppercase;
    font-weight: bold;

}
section button {
    display: block;
    border-style: none;
    background-color: black;
    color: white;
    width: 30px;
    height: 30px;
    font-size: 26px;
    outline: none;
    cursor: pointer;
}
section span {
    display: block;
    margin: auto;
    width: 20px;
  
}
.menu button.start {
    width: 70px;
    height: 30px;
    margin: 0 auto;
    border-style: none;
    background-color: black;
    color: white;
    cursor: pointer;    
    outline: none;

}
.menu button:hover {
    border: 2px solid white;
}
.menu button {
    border: 2px solid black;
}


